@media print {
  body {
    font-size: 12pt;
    line-height: 1.5;
    color: #000 !important;
    background: #fff !important;
  }

  header, nav, .header, .footer, .back-top-btn, .btn, .btn-link, .support, .app, .features, .about, .stats {
    display: none !important;
  }

  main, .container, .service, .service-list, .service-card, .card-content {
    width: 100% !important;
    box-shadow: none !important;
    color: #000 !important;
    background: none !important;
    margin: 0 !important;
    padding: 0 !important;
    display: block !important;
  }

  h1, h2, h3, h4, h5, h6 {
    page-break-after: avoid;
    color: #000 !important;
  }

  a {
    word-break: break-all;
    text-decoration: underline;
    color: #000 !important;
  }

  img, video {
    page-break-inside: avoid;
    max-width: 100% !important;
    height: auto !important;
  }

  ul, ol {
    padding-left: 2em;
  }

  .no-print {
    display: none !important;
  }

  @page {
    margin: 1.5cm;
    size: A4 portrait;
  }
}
